<template>
  <footer class="footer">
    <span class="scroll__top" @click="goTop" v-show="showScrollTop"></span>
    <div class="footer__top">
      <div class="container">
        <div class="footer__row">
          <div class="footer__getcall">
            <img src="../assets/img/tamasbama.png" alt="">
            <div class="footer__getcall-info">
              <h3 class="footer__getcall-h3">با ما تماس بگیرید</h3>
              <span class="gooter__getcall-span">برای ثبت سفارش و مشاوره با مشاوران ما تماس بگیرید !</span>
            </div>
          </div>
          <div class="footer__info">
            <span class="footer__info-span">Email : info@domian.com</span>
            <span class="footer__info-span margin--right-10">Tel : 1023233030</span>
          </div>
        </div>
      </div>
    </div>
    <div class="footer__main">
      <div class="container">
        <div class="footer__links">
          <a href="" class="footer__link">لینک اول</a>
          <a href="" class="footer__link">لینک اول</a>
          <a href="" class="footer__link">لینک اول</a>
          <a href="" class="footer__link">لینک اول</a>
          <a href="" class="footer__link">لینک اول</a>
          <a href="" class="footer__link">لینک اول</a>
        </div>
        <div class="footer__hr"></div>
        <div class="footer__about">
          <p class="footer__txt">
            این فروشگاه یک فروشگاه تستی است که با فریمورک Vue.js توسط احمد کاکایی طراحی و توسعه یافته است. قیمت ها واقعی نیستند!!!
          </p>
        </div>
      </div>
      <div class="footer__team">
        طراحی و توسعه توسط
        <a href="" class="footer__copy">احمد کاکایی</a>
        © 1400
      </div>
    </div>
    <div class="overlay" :class="{'overlay--is-active':showSideBar}"></div>
  </footer>

</template>

<script>
export default {
  name: "BaseFooter",
  data: () => {
    return {
      showScrollTop: false,
      scrollTimeout: null
    }
  },
  props: {
    showSideBar: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    handleScroll() {
      if (this.scrollTimeout) return;
      console.log('user scrolled')
      this.scrollTimeout = setTimeout(() => {
        this.showScrollTop = window.scrollY > 200;
        clearTimeout(this.scrollTimeout);
        this.scrollTimeout=0;
      }, 150)
    },
    goTop() {
      window.scrollTo({
        top: 0, behavior: 'smooth'
      })
    }
  },
  mounted() {
    document.addEventListener('scroll', this.handleScroll)
  }, destroyed() {
    document.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

